import React from 'react'
import '../css/Section3.css'
import GppGoodIcon from '@mui/icons-material/GppGood';
import SettingsSuggestIcon from '@mui/icons-material/SettingsSuggest';
import EngineeringIcon from '@mui/icons-material/Engineering';
import TipsAndUpdatesIcon from '@mui/icons-material/TipsAndUpdates';

const Section3 = () => {
  return (
    <div id="section_3" className='mainSection3'>
      <div className='opacity'></div>
        <div className='box_Section3'>
          <h2 className='title'>Productos TI</h2>
          <h3 className='subtitle'>Innovar con las tendencias tecnológicas</h3>
          <div className='cards'>
            <div className='card_content'>
              <GppGoodIcon/>
              <h2>Infocusnews</h2>
              <p>Diario digital que descubre información de valor en su compañía utilizando modelos estadísticos…</p>
              <a href="#">Leer Más</a>
            </div>
            <div className='card_content'>
              <EngineeringIcon/>
              <h2>ETL BI Business Intelligence</h2>
              <p>Servicio de gestión en nube o infraestructura propia del cliente para diseñar…</p>
              <a href="#">Leer Más</a>
            </div>
            <div className='card_content'>
              <SettingsSuggestIcon/>
              <h2>Motor de Sugerencias</h2>
              <p>El motor de sugerencia mide las preferencias de los clientes a cada una de ellas…</p>
              <a href="#">Leer Más</a>
            </div>
            <div className='card_content'>
              <TipsAndUpdatesIcon/>
              <h2>Innovación</h2>
              <p>Identificación de las preferencias individuales de sus clientes con máximo detalle…</p>
              <a href="#">Leer Más</a>
            </div>
          </div>
        </div>
    </div>
    
  )
}

export default Section3