import React, { useEffect } from 'react';
import wallpaper_1 from '../assets/img/wall_3.jpg';
import wallpaper_2 from '../assets/img/icon_header.png';
import '../css/Header.css';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';



const Header = () => {
  return (
    <div id="main_section" className='main'>
     
      <div className='box_header'>
        <div className='header_content'>
          <p className='slogan'>ETL Soluciones</p>
          <h1 className='title'>SOLUCIONES DE <span>ALTO IMPACTO</span></h1>
          <p className='phrase'>"Diez años transformando ideas en soluciones tecnológicas."</p>
          <a className='btn_services' href="#section_1">Explora nuestros Servicios</a>
         
        </div>
        <div className='image_header'> <img src={wallpaper_2} alt="" /></div>
      </div>
      
    </div>

  );
}

export default Header


