import React, { Component } from "react";
import logo from '../assets/img/etl-soluciones_logo.png';
import { Link } from 'react-scroll';
import "../css/Navbar.css";

class Navbar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            active: "nav__menu",
            icon: "nav__toggler"
        };
    }

    navToggle = () => {
        if (this.state.active === "nav__menu") {
            this.setState({ active: "nav__menu nav__active" });
        } else {
            this.setState({ active: "nav__menu" });
        }

        if (this.state.icon === "nav__toggler") {
            this.setState({ icon: "nav__toggler toggle" });
        } else {
            this.setState({ icon: "nav__toggler" });
        }
    };

    render() {
        return (
            <nav className="nav">
                <Link to="main" className="logo">
                    <img src={logo} alt='logo' />
                </Link>
                <ul className={this.state.active}>
                    <li className="nav__item">
                        <a href="#main_section" className="nav__link">
                            Inicio
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#section_1" className="nav__link">
                            Servicios
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#section_3" className="nav__link">
                            Productos
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#section_4" className="nav__link">
                            Sobre Nosotros
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#" className="nav__link">
                            Contacto
                        </a>
                    </li>
                </ul>
                <div onClick={this.navToggle} className={this.state.icon}>
                    <div className="line1"></div>
                    <div className="line2"></div>
                    <div className="line3"></div>
                </div>
            </nav>
        );
    }
}

export default Navbar;
