import React from 'react'
import '../css/Section4.css';
import img_about from '../assets/img/img_about.png'

const Section4 = () => {
    return (
        <div id="section_4" className='main_Section4'>
            <div className='box_content'>
                <div className='box_img'>
                    <img src={img_about} alt="developer" />
                </div>
                <div className='content'>
                    <h2>Sobre Nosotros</h2>
                    <p>Somos una empresa joven, con ganas y experiencia de profesionales con más de 10 años en el mercado de servicios de tecnología y Sistemas de información, contamos con la participación en proyectos de alto impacto, desarrollamos soluciones de TI aplicadas en sectores como comunicaciones, transporte, comercio, logística, solidario, salud, legal y servicios.</p>
                    <a className="btn_view gradient_buttons" href="">Saber Más</a>
                </div>
            </div>
        </div>
    )
}

export default Section4