import React, { useState, useEffect } from 'react';
import Confetti from 'react-confetti';
import '../css/Popup.css';
import anniversary from "../assets/img/img_aniver.png"
import CloseIcon from '@mui/icons-material/Close';

function Popup() {
    const [showPopup, setShowPopup] = useState(false);
    const [showConfetti, setShowConfetti] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => {
            setShowPopup(true);
            setShowConfetti(true);

            setTimeout(() => {
                setShowConfetti(false); 
            }, 6000); 
        }, 2000);

        return () => clearTimeout(timer);
    }, []);

    const closePopup = () => {
        setShowPopup(false);
    };

    return (
        <div className="popup-container">
            {showPopup && (
                <div className="popup-overlay">
                    <div className="popup-content show">
                        <button className="close-btn" onClick={closePopup}>
                            <span className="close-icon">
                                <CloseIcon />
                            </span>
                        </button>
                        <div className="confetti-container">
                            {showConfetti && (
                                <Confetti
                                    width={window.innerWidth}
                                    height={window.innerHeight}
                                    numberOfPieces={200}
                                    recycle={false}
                                    run={showConfetti}
                                />
                            )}
                        </div>
                        <img className='img_anniver' src={anniversary} alt="aniversario etl" />
                        <h1>FELIZ ANIVERSARIO ETL!!</h1>
                        <p>¡Celebramos una década de excelencia y compromiso! ETL Soluciones cumple 10 años liderando el camino en soluciones tecnológicas. Agradecemos a nuestros clientes y colaboradores por su confianza y apoyo en este viaje. ¡Juntos hacia un futuro aún más brillante!</p>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Popup;
