import React from 'react'
import img from '../assets/img/wall_section2.jpg'
import '../css/Section2.css'

const Section2 = () => {
    return (
        <div id="section_2" className='main_section2'>
            <div className='box_section2'>
                <img src={img} alt="developer" />
                <div className='txt_container'>
                    <h2>Proyectos</h2>
                    <h3>Requerimientos, Soporte y Mantenimiento</h3>
                    <h4>Manejo de Solicitudes de desarrollo</h4>
                    <p>- Liderazgo del proyecto end to end. <br />
                        - Gestión de Solicitudes: Incidencias, requerimientos y proyectos. <br />
                        - Construcción y liberación con “victorias tempranas”. <br />
                        - Piezas de software solventes, autónomas y de valor para el negocio. <br />
                        - Análisis de las inquietudes del negocios para traducirlas en iniciativas de tecnología de rápida y solvente aplicación. <br /></p>
                </div>
            </div>
        </div>
    )
}

export default Section2